:root {
    --background-color: #2b2b2b; /* Фон */
    --text-color: #ffffff;       /* Текст */
}


body {
    background-color: var(--background-color); /* Используем переменные */
    color: var(--text-color);
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
}

.app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

input {
    padding: 0.5rem;
    border: none;
    border-radius: 5px;
    margin-right: 1rem;
    width: 300px;
}

button {
    padding: 0.5rem 1rem;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
}

button:hover {
    background-color: #45a049;
}

ul {
    list-style: none;
    padding: 0;
    margin-top: 2rem;
    width: 100%;
    max-width: 500px;
}

li {
    background: #404040;
    padding: 0.5rem 1rem;
    margin: 0.5rem 0;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

li span {
    flex: 1;
}

li button {
    margin-left: 0.5rem;
}
